import { NextResponse } from 'next/server';
import { supabase } from '@/lib/supabase/client';

export async function POST(req: Request) {
  try {
    const { data: { user } } = await supabase.auth.getUser();

    if (!user) {
      return NextResponse.json({ message: 'Not authenticated' }, { status: 401 });
    }

    const params = await req.json();
    
    // Fetch the user's Jira configuration from Supabase
    const { data: config, error: configError } = await supabase
      .from('jira_configs')
      .select('*')
      .eq('user_id', user.id)
      .single();

    if (configError || !config) {
      return NextResponse.json({ message: 'Jira not configured' }, { status: 400 });
    }

    const { domain, email, api_token } = config;
    const projectKey = params.projectKey || 'KAN'; // Fallback project key
    const issueType = params.issueType || 'Task';

    // Call the real Jira Cloud REST API
    // Documentation: https://developer.atlassian.com/cloud/jira/platform/rest/v3/api-group-issues/#api-rest-api-3-issue-post
    const jiraResponse = await fetch(`https://${domain}/rest/api/3/issue`, {
      method: 'POST',
      headers: {
        'Authorization': `Basic ${Buffer.from(`${email}:${api_token}`).toString('base64')}`,
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({
        fields: {
          project: {
            key: projectKey
          },
          summary: params.summary,
          description: {
            type: "doc",
            version: 1,
            content: [
              {
                type: "paragraph",
                content: [
                  {
                    type: "text",
                    text: params.description || params.summary
                  }
                ]
              }
            ]
          },
          issuetype: {
            name: issueType
          }
        }
      })
    });

    if (!jiraResponse.ok) {
      const errorData = await jiraResponse.json();
      console.error('Jira API Error:', errorData);
      return NextResponse.json({ 
        message: errorData.errors ? Object.values(errorData.errors).join(', ') : 'Failed to create Jira issue' 
      }, { status: jiraResponse.status });
    }

    const result = await jiraResponse.json();

    return NextResponse.json({
      key: result.key,
      id: result.id,
      url: `https://${domain}/browse/${result.key}`
    });
  } catch (error: any) {
    console.error('Server Error:', error);
    return NextResponse.json({ message: error.message }, { status: 500 });
  }
}
